.accordion-enter {
  height: 0px;
  opacity: 0.01;
}

.accordion-enter.accordion-enter-active {
  opacity: 1;
  -webkit-transition: all 700ms;
  -o-transition: all 700ms;
  transition: all 700ms;
}

.accordion-leave {
  opacity: 1;
  height: 100%;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}

.accordion-leave.accordion-leave-active {
  opacity: 0.01;
  height: 0px;
}



